.category-wrapp {
  display: flex;
  gap: 2rem;
  margin-top: 3rem;
}
.category-wrapp .left-box {
  flex: 1;
}
.category-wrapp .left-box h1 {
  color: #72db73;
  font-family: Single Day;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: 139.688%; /* 99.178px */
  text-align: start;
  margin-bottom: 3.5rem;
}
.category-wrapp .left-box h2 {
  color: #fff;
  font-family: sans-serif;
  font-size: 58px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3; /* 94.988px */
  text-align: start;
}
.category-wrapp .right-box {
  flex: 1.2;
}
.categ-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
.categ-card {
  width: 250px;
  height: 250px;
  border-radius: 20px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
  border: 6px solid transparent;
}
.active-category {
  border: 6px solid #11b800;
}
.categ-card h1 {
  color: #fff;
  font-family: sans-serif;
  font-size: 36px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.next-page-btn {
  border-radius: 38.857px;
  background: #148a08;
  color: #fff;
  text-align: center;
  font-family: sans-serif;
  font-size: 26.286px;
  font-style: normal;
  font-weight: 500;
  line-height: 92.2%; /* 24.235px */
  letter-spacing: 0.526px;
  padding: 1rem 3rem;
  cursor: pointer;
  border: none;
  margin-bottom: 2rem;
}
.categ-close {
  border-radius: 38.857px;
  background: #148a08;
  color: #fff;
  text-align: center;
  font-family: sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 92.2%; /* 24.235px */
  letter-spacing: 0.526px;
  padding: 1rem 3rem;
  border: none;
  display: inline-block;
}
.categ-close span {
  padding-left: 4px;
  cursor: pointer;
  color: #085c00;
  font-family: sans-serif;
  font-size: 26.333px;
  font-style: normal;
  font-weight: 400;
  line-height: 105.688%; /* 27.831px */
  letter-spacing: 0.527px;
}
.cate-close-btn {
  display: grid;
  list-style: none;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 1rem;
}
.cate-close-btn li {
  margin-bottom: 1rem;
}
.alert-msg {
  display: flex;
  gap: 4px;
}
.alert-msg p {
  color: #f00;
  font-family: sans-serif;
  font-size: 25.14px;
  font-style: normal;
  font-weight: 400;
  line-height: 105.688%; /* 26.57px */
  letter-spacing: 0.503px;
}
.d-none {
  display: none;
}
.next-page-btnwrap {
  text-align: end;
  margin: 2rem 1rem;
}

@media only screen and (max-width: 600px) {
  .category-wrapp {
    flex-direction: column;
  }
  .category-wrapp .left-box h2 {
    font-size: 28px;
  }
  .category-wrapp .left-box h1 {
    margin-bottom: 1rem;
  }
  .alert-msg p {
    font-size: 18px;
    line-height: 1;
  }
  .alert-msg {
    align-items: center;
    margin-top: 1rem;
  }
  .categ-close {
    display: flex;
    height: 50px;
    width: fit-content;
    align-items: center;
    padding: 1rem 1.5rem;
  }
  .categ-card {
    width: 150px;
    height: 150px;
    padding: 0.5rem;
  }
  .categ-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  .categ-grid li {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .categ-card h1 {
    font-size: 20px;
  }
  .cate-close-btn li {
    margin-bottom: 0.5rem;
  }
  .cate-close-btn {
    gap: 0.5rem;
  }
  .next-page-btn {
    width: 100%;
    margin-bottom: 0;
  }
  .category-form {
    position: relative;
  }
  .next-page-btnwrap {
    text-align: end;
    margin: 1rem 1rem 0 0;
    position: fixed;
    bottom: 0;
    width: -webkit-fill-available;
    background-color: #000000d4;
    padding: 1rem;
  }
}
